.default-background {
  background-color: #6797A1;
  padding: 0;
  margin: 0;
}
.frontpage {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 0;
}
.frontheader {
  color: white;
  font-size: 50px;
  font-family: 'Libre Baskerville', serif;
  text-align: center;
}
.fronttext {
  color: white;
  font-size: 15px;
  font-family: 'Libre Baskerville', serif;
  text-align: center;
}
.frontbanner {
  top: 150px;
  position: absolute;
  animation: fadeIn 3s;
  text-align: center;
  width: 100%;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes transition {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
.exploretext {
  stroke: white;
  stroke-opacity: 1;
  fill-opacity: 1;
  fill: white;
  font-size: 15px;
  font-family: 'Libre Baskerville', serif;
}
.menucontainer {
  height: 80px;
  width: 80px;
  position: relative;
  float: left;
  cursor: pointer;
}
.menu {
  transform-style: preserve-3d;
  transition: transform 1s;
}
.menucontainer:hover .menu {
  transform: rotateY(180deg);
}
.menufront,
.menuback {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 80px;
  width: 80px;
}
.menufront {
  background: transparent url('../img/menu_white.svg') center center no-repeat;
  background-size: 60%;
  z-index: 2;
}
.menuback {
  background: white url('../img/menu_grey.svg') center center no-repeat;
  background-size: 60%;
  transform: rotateY(-180deg);
}
.menubar {
  position: sticky;
  perspective: 1000;
  height: 80px;
  width: 80px;
  margin-bottom: 10px;
}
.aboutpage {
  width: 100%;
  height: 100%;
  position: absolute;
  transition: transform 1s;
  margin-left: -100%;
  margin-top: -100%;
  background-color: #6797A1;
  z-index: 20;
}
.indexpage {
  width: 100%;
  height: 100%;
  position: absolute;
}
.aboutpage.visible {
  transition: all 0.8s ease-in-out;
  margin-left: 0%;
  margin-top: 0%;
}
.closecontainer {
  height: 80px;
  width: 80px;
  perspective: 1000;
  position: relative;
  float: left;
  cursor: pointer;
}
.close {
  transform-style: preserve-3d;
  transition: transform 1s;
}
.closecontainer:hover .close {
  transform: rotateY(180deg);
}
.closefront,
.closeback {
  backface-visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  height: 80px;
  width: 80px;
}
.closefront {
  background: transparent url('../img/multiply_white.svg') center center no-repeat;
  background-size: 60%;
  z-index: 20;
}
.closeback {
  background: white url('../img/multiply_grey.svg') center center no-repeat;
  background-size: 60%;
  transform: rotateY(-180deg);
}
.search {
  float: right;
  display: block;
  height: 80px;
  width: 80px;
  background: transparent url('../img/search_white.svg') center center no-repeat;
  background-size: 60%;
  cursor: pointer;
}
.searchtext {
  float: right;
  display: block;
  height: 40px;
  width: 30%;
  opacity: 0.8;
  margin-top: 15px;
  margin-right: 5px;
  border: 0;
}
.searchtext:active {
  border: solid 1px #707070;
  box-shadow: 0 0 5px 1px #969696;
  width: 40%;
  animation: textboxanimation 1s;
}
@keyframes textboxanimation {
  from {
    border: 0;
    width: 30%;
  }
  to {
    border: solid 1px #707070;
    box-shadow: 0 0 5px 1px #969696;
    width: 40%;
  }
}
h1.aboutpagetext {
  color: white;
  font-size: 30px;
  font-family: 'Libre Baskerville', serif;
  text-align: center;
  position: relative;
}
p.aboutpagetext {
  color: white;
  font-size: 15px;
  font-family: 'Libre Baskerville', serif;
  text-align: center;
  position: relative;
  padding: 30px;
}
.explorebutton {
  border-radius: 25px;
  width: 120px;
  height: 30px;
  border: 2px solid white;
  margin: 0px auto;
}
.explorebutton:hover {
  box-shadow: 0 0 20px white;
  background-color: #A38F84;
  opacity: 0.6;
}
.explorebutton p {
  color: white;
  text-align: center;
  line-height: 30px;
  font-family: 'Libre Baskerville', serif;
  margin: 0px;
  cursor: pointer;
}
